___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --headerGray: #474747;
    --btnDarkGray: #182226;
    --btnMenu: #DBDFD0;
}

html {
    height: 100%;
}

body {
    padding: 0;
    margin: 0;
    height: inherit;
    font-family: 'DM Sans';
}

* {
    box-sizing: border-box;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

ul, a {
    list-style: none;
    text-decoration: none;
    color: black;
}

#top-header {
    display: flex;
    background-color: var(--headerGray);
    justify-content: space-between;
    padding: 0 110px;
    height: 45px;
}

#top-header li {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
}

#top-header ul {
    padding: 0;
    margin: 0;
    display: flex;
    gap: 25px;
    color: white;
    justify-content: center;
}
 
#info > li img {
    height: 11px;
}

#info > li:first-child img {
    height: 15px;
}

#top-header > #social {
    gap: 2px;
}

#logo-head {
    display: flex;  
    text-wrap: nowrap;
}

#logo-head h1 {
    font-family: 'Playfair Display';
    font-weight: bold;
    font-style: italic;
    font-size: 42px;
    color: var(--headerGray);
    margin: 0;
}

#head-menu {
    display: flex;
    font-size: 15px;
    gap: 3px;
}

#head-menu li a {
    background-color: transparent;
    padding: 6px 18px;
    border-radius: 50px;
    transition: .15s ease-in-out;
}

#head-menu li a:hover {
    background-color: var(--btnMenu);
}

#bottom-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 105px;
}

.btn a {
    color: var(--btnDarkGray);
    font-weight: bold;
    display: flex;
    align-items: center;
    height: 50px;
    padding: 0 25px;
    border-radius: 50px;
    border: 2px solid var(--btnDarkGray);
    background-color: transparent;
    text-wrap: nowrap;
    transition: .15s ease-in-out;
}

.btn a:hover {
    background-color: var(--btnDarkGray);
    color: white;

